.form {
    display: block;
    padding: 10px;
    box-shadow: 2px 3px 8px 1px rgba(0, 0, 0, 0.3);
    font-size: 20px;
}

input {
    box-sizing: content-box;
    display: block;
    width: 100%;
    font-size: 24px;
    border-radius: 5px;
}

input:focus {
    outline: rgb(61, 72, 223);
    border: 2px solid rgb(61, 72, 223);
}

.btn {
    width: 100%;
    height: 30px;
    background-color: blue;
    color: #fff;
    font-size: 16px;
}